.booking_form .form_block .agree_search_checkbox .custom-control-label {
	font-size:12px;
}
.booking_form .form_block .agree_search_checkbox {
	display:block;
	margin:0px;
	padding:0px;
	overflow:hidden;
}
.booking_form .form_block .agree_search {
	padding:0px;
	margin:0px 0px;
}
.booking_form .form_block .agree_search_checkbox a {
	font-size:12px;
    color: #f8a826;
	text-decoration:underline;
}
.booking_form .submit-btn {
    color: #121947;
    background-color: #f8a826;
    height: 65px;
    border: none;
    width: 100%;
    display: block;
    text-transform: capitalize;
	font-size:20px;
	font-family: 'segoeui-bold';
}
@media only screen and (max-width:1200px) {
	.booking_form .form_block {
		padding: 1.5em 2em 2em;
	}
}
@media only screen and (max-width:767px) {
	.header_bg .navbar-default .navbar-toggle {
		border-color: #121947;
	}
	.header_bg .navbar-default .navbar-toggle .icon-bar {
		background-color: #121947;
	}
	.header_bg .navbar-default .navbar-toggle:hover,.header_bg .navbar-default .navbar-toggle:focus {
		background-color: transparent;
	}
	.header_bg #navbar.navbar-collapse {
		 position: absolute;
		width: 100%;
		background: #fff !important;
		z-index: 9999;
	}
	.booking_home.section {
		height:100%;
	}
	.home_page {
		position: inherit;
		top: 0%;
		left: 0;
		right: 0;
		-webkit-transform: translateY(0%);
		transform: translateY(0%);
		padding:4em 2em;
	}
	.booking_form .form_block .agree_search {
		margin:0px 0px 20px;
	}
}
@media only screen and (max-width:480px) {
	.home_page {
		padding:2em 0em;
	}
}



